import axios from 'axios'

export default {
    created: function() {
        /*  */
        /*
        if(this.$store.state.answerData == null) {
            this.$set(this.$store.state, 'answerData', Array
                .apply(null, Array(this.getTotalLength()))
                .map(function () { return -1; }));
        }
        */
       /* TODO: 30 */
       if(this.$store.state.answerData == null) {
        this.$set(this.$store.state, 'answerData', Array(30));
       }
    },
    methods: {

        /*  to i(), j() */
        q2ij: function(q, categories) {
            return this.q2ijMap.get(q, categories);
        },
        /* i(), j to () */
        ij2q: function(i, j, categories) {
            return this.ij2qTable(categories)[i][j];
        },
        /* ij2label ,  */
        ij2label: function(i, j) {
            const answerIndex = this.$store.state.answerData[this.ij2q(i, j)];
            if(answerIndex<0) return '';
            return this.$store.state.json.options[answerIndex];
        },
        /*  */
        getScore: function () {
            return this.getScores().reduce((acc, val) => acc + val);
        },
        
        /*  */
        q2ijMap: function(categories) {
            let cnt = 0;
            const retVal = new Map();
            for(let i=0; i<categories.length; i++) {
                for(let j=0; j<categories[i].q.length; j++) {
                    retVal.set(cnt++, [i, j]);
                }
            }
            return retVal;
        },
        ij2qTable: function(categories) {
            const retVal = [];
            let cnt = 0;
            for(let i=0; i<categories.length; i++) {
                retVal.push([]);
                for(let j=0; j<categories[i].q.length; j++) {
                    retVal[i].push(cnt++);
                }
            }
            return retVal;
        },
    }

}